@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.card {
  position: relative;
  text-align: center;
  width: 46vw;
  max-width: 400px;
  height: 33vw;
  max-height: 284px;
  border-radius: 24px;
  overflow: hidden;
  cursor: pointer;
}

.card img {
  position: relative;
  width: 100%;
  height: 100%;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(17, 17, 17, 0.6) 100%);
}

.card:hover .overlay {
  opacity: 1;
}

.description {
  position: absolute;
  bottom: 0;
  color: #FFFFFF;
  padding: 0 min(4vw, 33px);
}

.description h4 {
  font-size: min(3vw, 16px);
  line-height: min(3vw, 19px);;
}

.description p {
  justify-self: end;
  font-size: min(5vw, 28px);
  line-height: min(5vw, 33px);;
}

.big {
  grid-row: span 2;
  height: 70vw;
  max-height: 600px;
}
.cards-by-day {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 16px 0 32px 0;
}

.title {
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;
  text-align: center;
  color: #000000;
}

.card-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: min(4vw, 32px);
  gap: min(4vw, 32px);
  margin: min(2vw, 16px);;
}

.link {
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 51px;
  margin: 0 4px;
  padding: 16px 20px;
  border: none;
  border-radius: 24px;
  font-family: inherit !important;
  font-size: 16px;
  font-weight: bold;
  line-height: 19px;
}

.link.active {
  background-color: black;
  color: white;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: auto;
  min-height: 83px;
}

.links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin: 4px 8px;
}

.searchbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  width: 944px;
  height: 51px;
  margin: 4px 8px;
  padding: 0px 8px;
  background: #efefef;
  border-radius: 24px;
}

.searchbar i {
  margin: 0 9px 0 17px;
  font-size: 13px;
}

.searchbar input {
  width: 100%;
  border: none;
  background-color: transparent;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #333333;
}

.icons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 100px;
  height: 51px;
  margin: 4px 34px 4px 40px;
}

.icons i {
  font-size: 22px;
}

